

import React, { useState } from 'react';
import imageCompression from 'browser-image-compression';
import { uploadImage, createPost } from '../../services/uploadService'; // Import the new service
import '../../css/Posts/UploadTab.css';

const UploadTab = ({ configData }) => {
    const [description, setDescription] = useState(''); // For description
    const [selectedCategory, setSelectedCategory] = useState(''); // For category ID
    const [selectedLanguage, setSelectedLanguage] = useState(''); // For selected language
    const [selectedSangh, setSelectedSangh] = useState(''); // For selected Sangh
    const [scheduledTime, setScheduledTime] = useState(''); // For liveAt time
    const [imageFile, setImageFile] = useState(null); // For image file
    const [originalSize, setOriginalSize] = useState(null); // For original image size
    const [compressedSize, setCompressedSize] = useState(null); // For compressed image size
    const [isUploading, setIsUploading] = useState(false);
    const [uploadSuccess, setUploadSuccess] = useState(false);

    // Check if all fields are filled
    const isFormComplete =  scheduledTime && imageFile && selectedCategory && selectedSangh && selectedLanguage;

    // Handle file selection and compression
    const handleFileChange = async (e) => {
        const file = e.target.files[0];
        if (file) {
            const originalFileSize = (file.size / 1024).toFixed(2); // Size in KB
            setOriginalSize(originalFileSize);

            const options = {
                maxSizeMB: 0.05, // Aggressive lossy compression
                useWebp: true,
                fileType: 'image/webp',
                alwaysKeepResolution: true,
            };

            try {
                const compressed = await imageCompression(file, options);
                const compressedFileSize = (compressed.size / 1024).toFixed(2); // Size in KB
                setCompressedSize(compressedFileSize);
                setImageFile(compressed);
            } catch (error) {
                console.error('Error compressing the image:', error);
            }
        }
    };

    // Handle the upload and post creation process
    const handleUpload = async () => {
        if (!isFormComplete) return;
    
        setIsUploading(true);
        try {
            // Step 1: Upload the compressed image
            const uploadResponse = await uploadImage(imageFile, 'IMAGE');
            const fileUrl = uploadResponse; // Assuming the response contains the URL
    
            // Step 2: Create the post with the file URL and user inputs
            const postData = {
                description: "String",// Use the description state
                language: selectedLanguage, // Use the selected language from dropdown
                mediaType: 'IMAGE',
                liveAt: new Date(scheduledTime).getTime(), // Convert scheduledTime to timestamp
                categoryIds: [selectedCategory], // Pass the selected category ID as an array
                sangh: selectedSangh, // Use the selected sangh from dropdown
                political:false,
                url: fileUrl, // Set the uploaded image URL
            };
    
            console.log('Post Data:', JSON.stringify(postData, null, 2)); // Log post data
    
            // Step 3: Send the post request
            await createPost(postData);
            setUploadSuccess(true);
    
        } catch (error) {
            console.error('Error during upload or post creation:', error);
        } finally {
            setIsUploading(false);
        }
    };

    return (
        <div className="upload-pane">
            {/* Input for description */}
            {/* <div className="form-group">
                <label htmlFor="description">Description</label>
                <textarea
                    id="description"
                    className="input-field"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    placeholder="Enter description here..."
                />
            </div> */}

            {/* Dropdown for selecting a language */}
            <div className="form-group">
                <label htmlFor="languageSelect">Select Language</label>
                <select
                    id="languageSelect"
                    className="input-field"
                    value={selectedLanguage} // Bind this to selectedLanguage state
                    onChange={(e) => setSelectedLanguage(e.target.value)} // Update state with selected language
                >
                    <option value="">Select a language</option>
                    {configData?.languages?.map((language) => (
                        <option key={language} value={language}>
                            {language}
                        </option>
                    ))}
                </select>
            </div>

            {/* Dropdown for selecting a Sangh */}
            <div className="form-group">
    <label htmlFor="sanghSelect">Select Sangh</label>
    <select
        id="sanghSelect"
        className="input-field"
        value={selectedSangh} // Bind this to selectedSangh state
        onChange={(e) => setSelectedSangh(e.target.value)} // Update state with selected sangh
    >
        <option value="">Select a sangh</option>
        {configData?.sanghInfo?.map((sanghInfo) => (
            <option key={sanghInfo.sanghId} value={sanghInfo.sanghId}>
                {sanghInfo.name} {/* Show the name in the dropdown */}
            </option>
        ))}
    </select>
</div>


            {/* Dropdown for selecting a category */}
            <div className="form-group">
                <label htmlFor="categorySelect">Select Category</label>
                <select
                    id="categorySelect"
                    className="input-field"
                    value={selectedCategory} // Bind this to selectedCategory state
                    onChange={(e) => setSelectedCategory(e.target.value)} // Update state with selected category ID
                >
                    <option value="">Select a category</option>
                    {configData?.categories?.map((category) => (
                        <option key={category.categoryId} value={category.categoryId}> {/* Use category ID as value */}
                            {category.name} {/* Display category name */}
                        </option>
                    ))}
                </select>
            </div>

            {/* Date and time selector */}
            <div className="form-group">
                <label htmlFor="scheduleTime">Schedule Post Time</label>
                <input
                    type="datetime-local"
                    id="scheduleTime"
                    value={scheduledTime}
                    onChange={(e) => setScheduledTime(e.target.value)}
                />
            </div>

            {/* File input for selecting an image */}
            <div className="form-group">
                <label htmlFor="imageUpload">Select Image</label>
                <input
                    type="file"
                    id="imageUpload"
                    accept="image/*"
                    onChange={handleFileChange}
                />
            </div>

            {/* Display file sizes */}
            {originalSize && <p>Original Image Size: {originalSize} KB</p>}
            {compressedSize && <p>Compressed Image Size: {compressedSize} KB</p>}

            {/* Upload button */}
            <button
                className="button"
                onClick={handleUpload}
                disabled={!isFormComplete || isUploading}
            >
                {isUploading ? 'Uploading...' : 'Upload'}
            </button>

            {/* Success message */}
            {uploadSuccess && <p>Upload and post creation successful!</p>}
        </div>
    );
};

export default UploadTab;
