import React, { useState } from 'react';
import { fetchPosts, updatePostOrder } from '../../services/postService';
import '../../css/Posts/OrderPostsTab.css';

const OrderPostsTab = ({ configData }) => {

    const [selectedcategory, setSelectedcategory] = useState('');
    const [selectedLanguage, setSelectedLanguage] = useState(''); // For selected language
    const [selectedSangh, setSelectedSangh] = useState(''); 
    
    const [posts, setPosts] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isReordering, setIsReordering] = useState(false);
    const [postOrder, setPostOrder] = useState([]);

    const handleFetchPosts = async () => {
        if (!selectedcategory || !selectedSangh || !selectedLanguage) {
            alert('Please select category, sangh, and language.');
            return;
        }
        
        // If the postOrder has been modified, save the changes before fetching again
        if (postOrder.length && posts.some((post, index) => post.postId !== postOrder[index])) {
            await handleUpdateOrder();  // Call to update order
        }

        setIsLoading(true);
        try {
            const fetchedPosts = await fetchPosts(selectedcategory, selectedSangh, selectedLanguage); // Pass selected language
            console.log('Fetched posts:', fetchedPosts);  
            setPosts(fetchedPosts);
            setPostOrder(fetchedPosts.map((post) => post.postId)); // Store initial post order
        } catch (error) {
            console.error('Error fetching posts:', error);
        } finally {
            setIsLoading(false);
        }
    };
        
    const handleDragStart = (e, index) => {
        e.dataTransfer.setData('postIndex', index);
    };

    const handleDrop = (e, index) => {
        const draggedIndex = e.dataTransfer.getData('postIndex');
        const reorderedPosts = [...posts];
        const [draggedPost] = reorderedPosts.splice(draggedIndex, 1);
        reorderedPosts.splice(index, 0, draggedPost);
        setPosts(reorderedPosts);
        setPostOrder(reorderedPosts.map((post) => post.postId)); // Update post order
    };

    const handleDragOver = (e) => {
        e.preventDefault();
    };

    const handleUpdateOrder = async () => {
        if (!selectedcategory || !selectedSangh || !selectedLanguage) {
            alert('Please select category, sangh, and language.');
            return;
        }

        setIsReordering(true);
        try {
            const result = await updatePostOrder(postOrder, selectedcategory, selectedSangh, selectedLanguage);  // Pass category, sangh, language
            console.log('Server response after updating order:', result); 
            alert('Post order updated successfully.');
        } catch (error) {
            console.error('Error updating post order:', error);
        } finally {
            setIsReordering(false);
        }
    };

    return (
        <div className="order-posts-tab">
            {/* Dropdown for selecting a language */}
            <div className="form-group">
                <label htmlFor="languageSelect">Select Language</label>
                <select
                    id="languageSelect"
                    className="input-field"
                    value={selectedLanguage} // Bind this to selectedLanguage state
                    onChange={(e) => setSelectedLanguage(e.target.value)} // Update state with selected language
                >
                    <option value="">Select a language</option>
                    {configData?.languages?.map((language) => (
                        <option key={language} value={language}>
                            {language}
                        </option>
                    ))}
                </select>
            </div>
            {/* Dropdown for selecting a Sangh */}
            <div className="form-group">
    <label htmlFor="sanghSelect">Select Sangh</label>
    <select
        id="sanghSelect"
        className="input-field"
        value={selectedSangh} // Bind this to selectedSangh state
        onChange={(e) => setSelectedSangh(e.target.value)} // Update state with selected sangh
    >
        <option value="">Select a sangh</option>
        {configData?.sanghInfo?.map((sanghInfo) => (
            <option key={sanghInfo.sanghId} value={sanghInfo.sanghId}>
                {sanghInfo.name} {/* Show the name in the dropdown */}
            </option>
        ))}
    </select>
</div>

            <div className="form-group">
                <label htmlFor="categorySelect">Select Category</label>
                <select
                    id="categorySelect"
                    className="input-field"
                    value={selectedcategory}
                    onChange={(e) => setSelectedcategory(e.target.value)}
                >
                    <option value="">Select a category</option>
                    {configData?.categories?.map((category) => (
                        <option key={category.categoryId} value={category.categoryId}>
                            {category.name}
                        </option>
                    ))}
                </select>
            </div>

            {/* Fetch and Update buttons in the same row */}
            <div className="button-row">
                <button className="button" onClick={handleFetchPosts} disabled={isLoading}>
                    {isLoading ? 'Fetching...' : 'Fetch Posts'}
                </button>

                {posts.length > 0 && (
                    <button className="button" onClick={handleUpdateOrder} disabled={isReordering}>
                        {isReordering ? 'Updating...' : 'Update Post Order'}
                    </button>
                )}
            </div>

            {/* Display posts and allow reordering */}
            {posts.length > 0 && (
                <div className="post-list">
                    {posts.map((post, index) => (
                        <div
                            key={post.postId}
                            className="post-item"
                            draggable
                            onDragStart={(e) => handleDragStart(e, index)}
                            onDrop={(e) => handleDrop(e, index)}
                            onDragOver={handleDragOver}
                        >
                            <img src={post.url} alt="Post" className="post-image" />
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
};

export default OrderPostsTab;

