export const updatePostOrder = async (postIds, categoryId, sangh, language) => {
    const queryString = postIds.map((id) => `postIds=${id}`).join('&');  // Construct the postIds part of the query string
    const url = `/post/v1/post/order?${queryString}&categoryId=${categoryId}&sangh=${sangh}&language=${language}`;  // Append categoryId, sangh, and language
    
    try {
        const response = await apiService.put(url);  // Make the PUT request using the constructed URL
        return response.data;  // Assuming the API returns data
    } catch (error) {
        console.error('Error updating post order:', error);  // Log any error that occurs
        throw error;  // Re-throw the error for further handling if necessary
    }
};


