// Add a new category
export const addCategory = async (categoryName) => {
    const url = '/v1/category'; // Adjust endpoint as per your API documentation
    const requestBody = {
        name: categoryName, // The new category name provided by the user
    };

    try {
        const response = await apiService.post(url, requestBody);
        return response.data; // Assuming successful response contains category data
    } catch (error) {
        console.error('Error adding category:', error);
        throw error;
    }
};


// Delete a post by postId
export const deletePost = async (postId) => {
    const response = await apiService.delete(`/post/v1/post/${postId}`);
    return response.data;
};
export const deleteCategory = async (categoryId) => {
    const response = await apiService.delete(`/v1/category?categoryId=${categoryId}`);
    return response.data;
};

