import React, { useState } from 'react';
import imageCompression from 'browser-image-compression';
import { uploadImage, AddSangh } from '../../services/uploadService'; // Import the new service
import '../../css/Posts/UploadTab.css';

const SanghUploadTab = () => {
    const [sanghId, setSanghId] = useState(''); // For sangh ID
    const [sanghName, setSanghName] = useState(''); // For sangh name
    const [imageFile, setImageFile] = useState(null); // For image file
    const [isUploading, setIsUploading] = useState(false);
    const [uploadSuccess, setUploadSuccess] = useState(false);

    // Check if all fields are filled
    const isFormComplete = sanghId && sanghName && imageFile;

    // Handle file selection and compression
    const handleFileChange = async (e) => {
        const file = e.target.files[0];
        if (file) {
            const options = {
                maxSizeMB: 0.05, // Aggressive lossy compression
                useWebp: true,
                fileType: 'image/webp',
                alwaysKeepResolution: true,
            };

            try {
                const compressed = await imageCompression(file, options);
                setImageFile(compressed);
            } catch (error) {
                console.error('Error compressing the image:', error);
            }
        }
    };

    // Handle the upload and post creation process
    const handleUpload = async () => {
        if (!isFormComplete) return;

        setIsUploading(true);
        try {
            // Step 1: Upload the compressed image
            const uploadResponse = await uploadImage(imageFile, 'IMAGE');
            const fileUrl = uploadResponse; // Assuming the response contains the URL

            // Step 2: Create the post with the file URL and user inputs
            const postData = {
                sanghId: sanghId, // Add sangh ID from user input
                name: sanghName, // Add sangh name from user input
                imageUrl: fileUrl, // Set the uploaded image URL
            };

            console.log('Post Data:', JSON.stringify(postData, null, 2)); // Log post data

            // Step 3: Send the post request
            await AddSangh(postData);
            setUploadSuccess(true);

        } catch (error) {
            console.error('Error during upload or post creation:', error);
        } finally {
            setIsUploading(false);
        }
    };

    return (
        <div className="upload-pane">
            {/* Input for Sangh ID */}
            <div className="form-group">
                <label htmlFor="sanghId">Sangh ID</label>
                <input
                    type="text"
                    id="sanghId"
                    className="input-field"
                    value={sanghId}
                    onChange={(e) => setSanghId(e.target.value)}
                    placeholder="Enter Sangh ID..."
                />
            </div>

            {/* Input for Sangh Name */}
            <div className="form-group">
                <label htmlFor="sanghName">Sangh Name</label>
                <input
                    type="text"
                    id="sanghName"
                    className="input-field"
                    value={sanghName}
                    onChange={(e) => setSanghName(e.target.value)}
                    placeholder="Enter Sangh Name..."
                />
            </div>

            {/* File input for selecting an image */}
            <div className="form-group">
                <label htmlFor="imageUpload">Select Image</label>
                <input
                    type="file"
                    id="imageUpload"
                    accept="image/*"
                    onChange={handleFileChange}
                />
            </div>

            {/* Upload button */}
            <button
                className="button"
                onClick={handleUpload}
                disabled={!isFormComplete || isUploading}
            >
                {isUploading ? 'Uploading...' : 'Upload'}
            </button>

            {/* Success message */}
            {uploadSuccess && <p>Upload and post creation successful!</p>}
        </div>
    );
};

export default SanghUploadTab;
