import React, { useState } from 'react';
import UploadTab from '../Posts/UploadTab';
import OrderPostsTab from '../Posts/OrderPostsTab';
import DeletePostsTab from '../Posts/DeletePostsTab';
import CategoryPostsTab from '../Posts/AddCategories';
import DeleteCategoryTab from '../Posts/DeleteCategory';
import SanghUploadTab from '../Posts/AddSangh';

const PostsTab = ({ configData }) => {
    const [activeSubTab, setActiveSubTab] = useState('upload'); // Default to the Upload sub-tab

    const renderPostSubTab = () => {
        switch (activeSubTab) {
            case 'upload':
                return <UploadTab configData={configData} />;
            case 'order':
                return <OrderPostsTab configData={configData} />;
            case 'delete':
                return <DeletePostsTab configData={configData} />;
             case 'Category':
                 return <CategoryPostsTab configData={configData} />;
            case 'DeleteCategory':
                return <DeleteCategoryTab configData={configData} />;
                case 'AddSangh':
                    return <SanghUploadTab configData={configData} />;
            default:
                return null;
        }
    };

    return (
        <div>
            <div className="sub-tabs">
                <button className={`tab-item ${activeSubTab === 'upload' ? 'active-tab' : ''}`} onClick={() => setActiveSubTab('upload')}>
                    Upload
                </button>
                <button className={`tab-item ${activeSubTab === 'order' ? 'active-tab' : ''}`} onClick={() => setActiveSubTab('order')}>
                    Order Posts
                </button>
                <button className={`tab-item ${activeSubTab === 'delete' ? 'active-tab' : ''}`} onClick={() => setActiveSubTab('delete')}>
                    Delete Posts
                </button>
                <button className={`tab-item ${activeSubTab === 'Category' ? 'active-tab' : ''}`} onClick={() => setActiveSubTab('Category')}>
                    Add Category
                </button>
                <button className={`tab-item ${activeSubTab === 'DeleteCategory' ? 'active-tab' : ''}`} onClick={() => setActiveSubTab('DeleteCategory')}>
                    Delete Category
                </button>
                <button className={`tab-item ${activeSubTab === 'AddSangh' ? 'active-tab' : ''}`} onClick={() => setActiveSubTab('AddSangh')}>
                    Add Sangh
                </button>
            </div>
            <div className="tab-content">
                {renderPostSubTab()}
            </div>
        </div>
    );
};

export default PostsTab;
