import React, { useState, useEffect } from 'react';
import '../../css/Posts/DeletePostsTab.css'; // Import your CSS
import { deleteCategory } from '../../services/postService'; // Import the service call for deletion

const DeleteCategoryTab = ({ configData }) => {
    const [selectedCategory, setSelectedCategory] = useState(''); // Selected category ID
    const [isDeleting, setIsDeleting] = useState(false); // Loading state for deleting a category

    // Handle deleting a selected category
    const handleDeleteCategory = async () => {
        if (!selectedCategory) {
            alert('Please select a category.');
            return;
        }

        const confirmDelete = window.confirm('Are you sure you want to delete this category?');
        if (confirmDelete) {
            setIsDeleting(true);
            try {
                await deleteCategory(selectedCategory); // Pass the categoryId to delete
                alert('Category deleted successfully!');
                // Optionally, remove the deleted category from the dropdown after successful deletion
            } catch (error) {
                console.error('Error deleting category:', error);
                alert('Failed to delete category.');
            } finally {
                setIsDeleting(false);
            }
        }
    };

    return (
        <div className="delete-category-tab">
            <div className="form-group">
                <label htmlFor="categorySelect">Select Category to Delete</label>
                <select
                    id="categorySelect"
                    className="input-field"
                    value={selectedCategory}
                    onChange={(e) => setSelectedCategory(e.target.value)} // Update selected categoryId
                >
                    <option value="">Select a category</option>
                    {configData?.categories?.map((category) => (
                        <option key={category.categoryId} value={category.categoryId}>
                            {category.name}
                        </option>
                    ))}
                </select>
            </div>

            {/* Delete button */}
            <button className="button" onClick={handleDeleteCategory} disabled={isDeleting || !selectedCategory}>
                {isDeleting ? 'Deleting...' : 'Delete Category'}
            </button>
        </div>
    );
};

export default DeleteCategoryTab;
