import React, { useState } from 'react';
import '../../css/Posts/DeletePostsTab.css'; // Update your CSS as needed
import { addCategory } from '../../services/postService'; // Import your addCategory service function

const AddCategoryTab = () => {
    const [newCategory, setNewCategory] = useState(''); // New category input
    const [addCategoryLoading, setAddCategoryLoading] = useState(false); // Loading state for adding a category



    // Handle adding a new category using the imported API function
    const handleAddCategory = async () => {
        if (!newCategory) {
            alert('Please enter a category name.');
            return;
        }
        setAddCategoryLoading(true);
        try {
            const result = await addCategory(newCategory); // Use the API service function
            alert(`Category "${result.name}" added successfully!`);
            setNewCategory('');  // Clear the input after successful addition
        } catch (error) {
            console.error('Error adding category:', error);
            alert('Failed to add category.');
        } finally {
            setAddCategoryLoading(false);
        }
    };

    return (
        <div className="add-category-tab">
            <div className="form-group">
                <label htmlFor="newCategory">Add New Category</label>
                <input
                    type="text"
                    id="newCategory"
                    className="input-field"
                    value={newCategory}
                    onChange={(e) => setNewCategory(e.target.value)} // Bind input to newCategory state
                />
                <button className="button" onClick={handleAddCategory} disabled={addCategoryLoading}>
                    {addCategoryLoading ? 'Adding...' : 'Add Category'}
                </button>
            </div>
        </div>
    );
};

export default AddCategoryTab;
